<template>
  <div class="header">

    <el-menu theme="dark" class="el-menu-demo" mode="horizontal" @select="handleSelect">
      <div class="logo">
        <img
          src="data:image/svg+xml;base64,"
          alt="element-logo" class="nav-logo">
      </div>
      <el-submenu index="3">
        <template slot="title">{{username}}(系统管理员)</template>
        <el-menu-item index="2-1" @click="signOut">退出登录</el-menu-item>
      </el-submenu>
      <!--<div></div>-->
    </el-menu>
    <div class="line"></div>
  </div>

</template>

<script type="text/ecmascript-6">
import $ from 'jquery'
import config from '../../configFile.js'
  export default {
    data() {
      return {
        activeIndex: '1',
        username: window.localStorage.getItem('username')
      }
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      signOut() {
        var _this = this;

             $.ajax({
              url: config.url + '/admin/logout',
              type: 'post',

              xhrFields: {
            withCredentials: true
        },
              success: function (data) {
                  console.log("-----------------data", data);
                          _this.$router.push({path: '/'})
              },
              error: function (xhr, status, error) {
                  console.log('Error: ' + error.message);
              },
            });
          // window.localStorage.setItem('username', this.username)
          // this.$router.push({path: '/home'})
        

      }

    }
  }

</script>
<style lang="scss" scoped>
  .logo {
    outline: none;
  }

  .header {
    width: 100%;
    position: fixed;
    z-index: 99;
    .el-menu-demo {
      width: 100%;
      min-width: 500px;
      border-radius: 0;
      display: flex;
      .menuSub {
        flex: 6;
        position: relative;
      }
      .logo {
        flex: 1;
        position: relative;
        top: 10px;
        left: 10px;
      }
    }
  }

</style>



